/**
 * 用户拒绝
 * @param error
 * @returns
 */
function isUserRejectedError(error: Error) {
  console.log('🚀🚀🚀 ~ cross-error.ts:8 ~ isUserRejectedError ~ error:', error.message)

  console.log('🚀🚀🚀 ~ cross-error.ts:8 ~ isUserRejectedError ~ error2:', error.message.toLocaleLowerCase().includes('user'))
  console.log('🚀🚀🚀 ~ cross-error.ts:8 ~ isUserRejectedError ~ error3:', error.message.toLocaleLowerCase().includes('reject'))

  try {
    const errorString = error.message.toLocaleLowerCase()
    return errorString.includes('user') && errorString.includes('reject')
  } catch (error1) {
    return false
  }
  return false
}

type ErrorMatcher = (error: any) => boolean
type ErrorMessage = string | ((error: any) => string)

const errorMap: { matcher: ErrorMatcher; message: ErrorMessage }[] = [
  { matcher: isUserRejectedError, message: 'User rejected the request' },
  {
    matcher: (error: any) => String(error).includes('Transaction validator signing failed'),
    message: 'Oops. Failed to pull your txid. Please check your txn status from explorer or from your wallet.'
  }
]

export function handleCrossErrorMessages(error: any): string {
  for (const { matcher, message } of errorMap) {
    if (matcher(error)) {
      return typeof message === 'function' ? message(error) : message
    }
  }

  return 'Transaction failed'
}
