import { CrossSwapPlatform, CrossSwapToken, isEqualToken, isEqualTokenAddress } from '@cetusprotocol/cross-swap-sdk'
import { ChainType } from '@lifi/sdk'
import { LocalForageService } from './local-forage-service'

const CACHE_DURATION = 30 * 60 * 1000

interface CachedTokenList {
  tokenList: CrossSwapToken[]
  lastUpdated: number
}

class CrossTokensTableImpl extends LocalForageService {
  private static instances: Map<CrossSwapPlatform, CrossTokensTableImpl> = new Map()

  private constructor(platform: CrossSwapPlatform) {
    super('CetusSite', `crossSwapTokens_${platform}`)
  }

  public static getInstance(platform: CrossSwapPlatform): CrossTokensTableImpl {
    if (!this.instances.has(platform)) {
      this.instances.set(platform, new CrossTokensTableImpl(platform))
    }
    return this.instances.get(platform)!
  }
}

export class CrossTokensTable {
  static async setTokenList(platform: CrossSwapPlatform, chainId: number, tokenList: CrossSwapToken[]): Promise<void> {
    const data: CachedTokenList = {
      tokenList,
      lastUpdated: Date.now()
    }
    return CrossTokensTableImpl.getInstance(platform).setItem(chainId.toString(), data)
  }

  static async getTokenByAddress(platform: CrossSwapPlatform, chain_id: number, address: string): Promise<CrossSwapToken | null> {
    const tokenList = await CrossTokensTable.getAllTokenList(platform, chain_id)
    if (tokenList) {
      return tokenList.find(token => isEqualTokenAddress(token.address, address, token.type === ChainType.MVM)) || null
    }
    return null
  }

  static async getToken(platform: CrossSwapPlatform, token: CrossSwapToken): Promise<CrossSwapToken | null> {
    const tokenList = await CrossTokensTable.getAllTokenList(platform, token.chain_id)
    if (tokenList) {
      return tokenList.find(t => isEqualToken(t, token)) || null
    }
    return null
  }

  static async getAllTokenList(platform: CrossSwapPlatform, chainId: number): Promise<CrossSwapToken[] | null> {
    const data = (await CrossTokensTableImpl.getInstance(platform).getItem(chainId.toString())) as CachedTokenList | null
    if (!data) return null
    if (typeof data.lastUpdated !== 'number' || typeof data.tokenList === 'undefined') {
      await CrossTokensTableImpl.getInstance(platform).removeItem(chainId.toString())
      return null
    }
    if (Date.now() - data.lastUpdated > CACHE_DURATION) {
      // 超过缓存时间，清空表
      await CrossTokensTableImpl.getInstance(platform).removeItem(chainId.toString())
      return null
    }
    return data.tokenList
  }
}
